import React, { Fragment, useState } from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { login } from "../../actions/auth";
import auth from "../../reducers/auth";

export const Admin_Login = ({ login, isAuthenticated }) => {
  const [formData, setFormData] = useState({
    email: "",
    password: ""
  });

  const { email, password } = formData;

  const onChange = e =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = async e => {
    e.preventDefault();
    login(email, password);
  };

  //Redirect if loggedin

  if (isAuthenticated) {
    return <Redirect to="/adminposts" />;
  }

  return (
    <Fragment>
      <h1 className="large text-primary">Admin Sign In</h1>
      <form className="form" onSubmit={e => onSubmit(e)}>
        <div className="form-group">
          <input
            type="email"
            placeholder="Email Address"
            name="email"
            value={email}
            onChange={e => onChange(e)}
          />
        </div>
        <div className="form-group">
          <input
            type="password"
            placeholder="Password"
            name="password"
            value={password}
            onChange={e => onChange(e)}
            minLength="8"
          />
        </div>
        {email === "admin@admin.com" && password === "adminadmin" ? (
          <input type="submit" className="btn btn-primary" value="Log In" />
        ) : (
          <Redirect to="/adminlogin" />
        )}
      </form>
    </Fragment>
  );
};

Admin_Login.propTypes = {
  login: PropTypes.func.isRequired,
  isAuthenticated: PropTypes.bool
};

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});
export default connect(mapStateToProps, { login })(Admin_Login);
